@media (max-width: 768px) {
  .the-menu {
    grid-column-gap: 1rem;
    grid-gap: 2rem;
    margin-top: 2rem;
    grid-template-columns: 10rem 10rem 10rem;
  }

  .menu-page .content .title h1 {
    font-size: 2rem;
  }

  .menu-page .content .title p {
    font-size: 0.7rem;
    line-height: normal;
  }

  .menu {
    width: 10rem;
    height: 10rem;
  }

  .menu img {
    width: 8rem;
    margin-top: -0.5rem;
  }

  .nama-menu {
    font-size: 0.6rem;
  }

  .harga {
    font-size: 0.4rem;
    line-height: normal;
  }
}

@media (max-width: 426px) {
  .the-menu {
    grid-template-columns: 10rem 10rem !important;
  }
}

@media (max-width: 321px) {
  .the-menu {
    grid-template-columns: 10rem !important;
  }
}
