* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  width: 100%;
  height: 650px;
  left: 0px;
  border-radius: 0 0 20px 20px;
  top: 0px;
  background: #0f8b8d;
}

.jumbotron {
  width: 100%;
  height: 650px;
  left: 0px;
  border-radius: 0 0 20px 20px;
  top: 0px;
  background: #0f8b8d;
  z-index: -10;
  background: rgba(20, 54, 66, 1);
}

.coffe-jumbotron {
  opacity: 0.3;
  width: 100%;
  height: 650px;
  background-size: contain;
  background-repeat: no-repeat;
  z-index: -1;
  left: 0px;
  border-radius: 0 0 20px 20px;
  top: -20px;
}

.coffe-jumbotron img {
  width: 100%;
  height: 650px;
  border-radius: 0 0 20px 20px;
  object-fit: cover;
}

.container {
  padding: 0;
  margin: 0;
}

.Home-page .navbar {
  overflow: hidden;
}

.navbar-mobile {
  display: none;
}

.Home-page .navbar .logo {
  position: absolute;
  margin-left: 5rem;
  top: 39px;
  z-index: 10;
}

.logo img {
  height: 3.5rem;
}

.Home-page ul {
  position: absolute;
  z-index: 10;
  width: 119px;
  height: 36px;
  right: 37%;
  top: 54px;
  line-height: 36px;

  text-align: center;

  color: #f9fbfe;
  list-style: none;
  display: flex;
  flex-direction: row;
}

.Home-page .navbar ul {
  margin-right: 5rem;
}

li {
  margin: auto 2rem;
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.65rem;
}

a {
  text-decoration: none;
  color: #fff;
}

.navbar a {
  padding: 0.8rem;
}

.navbar a:hover,
.footer a:hover {
  border-bottom: 2px solid rgb(255, 255, 255);
}

.navbar a,
.footer a {
  border-bottom: 2px solid transparent;
}

.navbar a.active {
  border-bottom: 2px solid rgb(255, 255, 255);
}

.jumbotron h1 {
  width: 487px;
  height: 168px;

  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 70px;
  line-height: 84px;
  text-align: center;

  color: #f9fbfe;
}

.jumbotron p {
  height: 43px;

  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 43px;
  text-align: center;

  color: #f9fbfe;
}

.Home-page .title {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  bottom: 25rem;
  margin: auto;
}

section {
  padding: 200px 250px;
  background-color: #fff;
  overflow-x: hidden;
}

.Home-page .box {
  transform: translateX(400%);
  transition: transform 1.1s ease;
}

.Home-page .box:nth-of-type(even) {
  transform: translateX(-400%);
}

.Home-page .box.show {
  transform: translateX(0);
}

.who-are-we,
.product-showcase,
.hang-out {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  left: 257px;
  top: 800px;
}

.who-are-we img {
  width: 335px;
  height: 460px;
  left: 257px;
  top: 800px;
  background: #d9d9d9;
  border-radius: 20px;
  object-fit: cover;
}

.detail h1 {
  height: 58px;
  left: 730px;
  top: 800px;

  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 2.7rem;
  line-height: 58px;

  color: #0f8b8d;
}

.detail p {
  height: 116px;
  left: 730px;
  top: 903px;

  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 1.3rem;
  line-height: 29px;

  color: #143642;
}

.who-are-we .detail p {
  width: 452px;
}

.product-showcase .detail p {
  width: 425px;
}

.hang-out .detail p {
  width: 398px;
}

.detail hr {
  border: 3px solid #ec9a29;
  margin: 20px 0;
  width: initial;
}

.wrap {
  width: fit-content;
}

.product-showcase {
  margin: 200px 0;
}

.product-showcase img {
  width: 335px;
  height: 335px;
  border-radius: 20px;
  object-fit: cover;
  background: #d9d9d9;
}
.button {
  background: #a8201a;
  border-width: 0;
  border-radius: 55px;
  width: 18.5rem;
  height: 4rem;
  font-size: 1.3rem;
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  line-height: 29px;
  text-align: center;
  color: #f9fbfe;
  transition-duration: 0.4s;
  cursor: pointer;
}

.button:hover {
  background-color: #ba261e;
}

.hang-out img {
  width: 453px;
  height: 453px;
  object-fit: cover;
  border-radius: 20px;
}

.hang-out {
  padding-bottom: 200px;
}

.hang-out .detail {
  margin-left: 16%;
}

.find-us {
  padding: 5rem 0;
  padding-top: 0;
  text-align: center;
}

.find-us h1,
.ask-us h1 {
  color: #0f8b8d;
  font-family: "Lato";
  font-style: normal;
  font-weight: 800;
  font-size: 2.7rem;
  line-height: 58px;
}

.find-us a {
  color: #ec9a29;
  font-family: "Lato";
  font-style: normal;
  font-weight: 800;
  font-size: 2.7rem;
  line-height: 58px;
}

.feeds-1,
.feeds-2 {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.feeds-1 img,
.feeds-2 img {
  width: 237px;
  height: 237px;
}

.feeds-2 {
  margin-top: 4rem;
}

.ask-us p {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 1.3rem;
  line-height: 29px;
  padding: 1.5rem;
  /* identical to box height */

  text-align: center;

  color: #143642;
}

.ask-us {
  text-align: center;
  padding-top: 5rem;
}

.footer .logo img {
  top: 244.5rem !important;
}

/* CSS code for screens smaller than 768px (tablets and mobile devices) */

/* styles for screens smaller than 768px */
@media (max-width: 768px) {
  .header {
    height: 500px;
  }

  .mobile-menu-icon {
    width: 3rem;
    display: flex;
    position: absolute;
  }

  .navbar-mobile img {
    width: 1rem;
    margin: auto;
  }

  .Home-page .navbar .logo {
    top: 2rem;
    left: 2rem;
    margin-left: 0;
  }

  .Home-page .navbar .logo img {
    height: 2.5rem;
  }

  .jumbotron {
    height: 500px;
  }

  .jumbotron h1 {
    width: 44vmax;
    height: auto;
    font-size: 2.25rem;
    line-height: normal;
    margin: auto;
  }

  .jumbotron p {
    font-size: 1rem;
    line-height: normal;
  }

  .coffe-jumbotron {
    height: 500px;
  }

  .coffe-jumbotron img {
    height: 500px;
  }

  ul {
    margin-top: 10px !important;
    left: 12.5%;
    text-align: center;
    padding: 20px !important;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }

  ul li {
    display: block;
    margin-bottom: 10px;
    font-size: 1rem;
  }

  .title {
    margin-top: 14vh;
    text-align: center;
  }

  .Home-page section {
    padding: 2.5rem 2rem;
  }

  .box {
    padding: 20px;
  }

  .who-are-we,
  .product-showcase,
  .hang-out {
    flex-direction: column;
  }

  .box img {
    margin: auto;
    margin-bottom: 20px;
  }

  .who-are-we img {
    width: 10rem !important;
    height: 13.3rem !important;
  }

  .product-showcase img,
  .hang-out img {
    width: 10rem !important;
    height: 10rem !important;
  }

  .detail h1 {
    font-size: 1.25rem;
    line-height: normal;
    height: auto;
  }

  .detail hr {
    border: 2px solid #ec9a29;
    margin: 12px 0;
  }

  .detail p {
    height: auto;
    font-size: 0.75rem;
    line-height: normal;
  }

  .who-are-we .detail p,
  .product-showcase .detail p,
  .hang-out .detail p {
    width: fit-content;
  }

  .product-showcase {
    margin-top: 0;
    flex-direction: column-reverse;
    margin-bottom: 0;
  }

  .hang-out .detail {
    margin-left: 0;
  }

  .Home-page .button {
    display: block;
    margin-top: 20px;
    width: 8.4rem;
    height: 1.75rem;
    font-size: 0.625rem;
  }

  .find-us h1,
  .ask-us h1 {
    font-size: 1.25rem;
    line-height: normal;
  }

  .ask-us p {
    font-size: 0.75rem;
    line-height: normal;
  }

  .find-us a {
    font-size: 1.25rem;
    line-height: normal;
  }

  .ask-us .button {
    margin-top: 0;
  }

  .feeds-1,
  .feeds-2 {
    display: flex;
    gap: 2rem;
    justify-content: center;
  }

  .feeds-2 {
    margin-top: 0;
  }

  .feeds-1 img,
  .feeds-2 img {
    width: 6rem !important;
    height: 6rem !important;
    margin-bottom: 20px;
  }

  .ask-us {
    padding-top: 3rem;
    text-align: center;
  }

  .ask-us button {
    margin: auto;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .navbar-mobile {
    display: flex;
    position: absolute;
    top: 2.2rem;
    right: 2rem;
    background-color: white;
    border-radius: 3px;
    width: 1.5rem;
    height: 1.5rem;
  }

  .navbar-mobile img {
    width: 1rem;
    margin: auto;
  }

  .Home-page .navbar ul {
    width: fit-content;
    right: 5vw;
    left: 5vw;
    top: 7rem;
    justify-content: space-between;
    margin: auto;
  }

  section {
    padding: 5rem 4rem;
  }

  .who-are-we img {
    width: 16rem;
    height: 23.75rem;
  }

  .product-showcase img {
    width: 15rem;
    height: 15rem;
  }

  .hang-out img {
    width: 19rem;
    height: 19rem;
  }

  .detail {
    width: 50%;
  }

  .detail h1 {
    font-size: 2rem;
  }

  .detail p {
    font-size: initial;
    max-width: 100%;
  }

  .hang-out .detail {
    margin-left: 0 !important;
  }

  .feeds-1 img,
  .feeds-2 img {
    width: 12rem;
    height: 12rem;
  }
}

@media (min-width: 1600px) {
  section {
    padding: 10rem 50rem;
  }
}
