@media (min-width: 769px) and (max-width: 1024px) {
  .navbar {
    flex-direction: column;
    margin: auto;
    height: auto !important;
  }

  .the-link {
    padding: 2rem;
    margin-right: auto !important;
    margin-left: auto !important;
  }

  .navbar .logo {
    padding: 2rem;
    margin: 0 !important;
  }

  .logo img {
    margin: 0 !important;
    height: 2.5rem !important;
  }

  ul {
    margin: auto;
    width: 100%;
  }

  li {
    font-size: 1rem !important;
  }
}

@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    margin: auto;
    height: auto !important;
  }

  .logo {
    padding: 2rem;
    margin: 0 !important;
  }

  .the-link {
    width: 100%;
    margin: 0;
  }

  .logo img {
    margin: 0 !important;
    height: 2.5rem !important;
  }

  ul {
    margin: 20px auto !important;
    padding: 0 !important;
  }

  li {
    font-size: 1rem !important;
  }
}
