/* CONTENT */

/* TITLE */

.menu-page .content .title h1 {
  font-size: 2.7rem;
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 700;
  line-height: 58px;
  margin-top: 4rem;
}

.menu-page .content .title p {
  font-family: "Lato";
  font-style: normal;
  font-weight: 500;
  font-size: 1.3rem;
  line-height: 29px;
  padding: 1.5rem;
  color: #143642;
}

.content {
  text-align: center;
}

.content h1 {
  color: #0f8b8d;
}

.content p {
  color: #143642;
}

.menu {
  width: 12.8rem;
  height: 12.8rem;
  background-size: cover;
  /* background-image: url(./assets/Coffee\ background.png); */
  border-radius: 20px;
  margin: auto;
  display: flex;
  flex-direction: column;
  transition: flex-direction 1s ease;
}
.menu:hover {
  flex-direction: column-reverse;
}
.menu img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 186px;
  margin: auto;
  margin-top: 0.2rem;
}
.menu span img:hover {
  width: 200px;
}
.the-menu {
  display: grid;
  grid-template-columns: 12.8rem 12.8rem 12.8rem;
  justify-content: center;
  grid-area: 5rem;
  grid-gap: 3rem;
  grid-column-gap: 6rem;
  margin-top: 1rem;
  margin-bottom: 6rem;
}

.nama-menu {
  position: relative;
  width: 100%;
  height: 24px;
  /* left: 27%;
    top: 1rem; */
  margin-top: 1.2rem;
  font-family: "Lato";
  font-style: normal;
  font-weight: 700;
  font-size: 1.2rem;
  line-height: 24px;
  /* identical to box height */

  text-align: center;

  color: #f9fbfe !important;
}

.harga {
  position: relative;
  width: 100%;
  height: 22px;
  /* left: 29%;
    top: 3rem; */
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 22px;
  /* identical to box height */

  text-align: center;

  color: #f9fbfe !important;
}
